import React, { useEffect, useState, Touch, useRef } from 'react';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';
import './Slider.css';
import '../../styles/photos.css'
import { LazyLoadImage } from "react-lazy-load-image-component";

interface IProps {
  photos: IImage[];
  showWatermark?: boolean; 
  isBorderRounded?: boolean;
}

const cencitivity = 20;
var scrolling: "Page" | "Container" | undefined = undefined;

function Slider(props: IProps) {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [touchStart, setTouchStart] = useState<Touch>({} as any as Touch);
  const [touchEnd, setTouchEnd] = useState<Touch>({} as any as Touch);
  const [photos, setPhotos] = useState<IImage[]>(props.photos);
  const [showDots, setShowDots] = useState(false); 
  var element = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setPhotos([...props.photos]);
    setCurrentIndex(0);
    setShowDots(true); 
  }, [props.photos]);

  const handleTouchStart = (e: React.TouchEvent<HTMLImageElement>) => {
    scrolling = undefined;
    setTouchStart(e.targetTouches.item(0));
  };

  const handleTouchMove = (e: React.TouchEvent<HTMLImageElement>) => {
    if (Math.abs(touchStart.clientY - touchEnd.clientY) > cencitivity && scrolling === undefined) {
      scrolling = "Page";
    }
    if (Math.abs(touchStart.clientX - touchEnd.clientX) > cencitivity && scrolling === undefined) {
      scrolling = "Container";
      disableBodyScroll(element.current!);
    }
    setTouchEnd(e.targetTouches[0]);
  };

  const handleTouchEnd = (e: React.TouchEvent<HTMLImageElement>) => {
    const touchDiff = touchStart?.clientX - touchEnd?.clientX;
    const threshold = 25;
    enableBodyScroll(element.current!);
    if (scrolling !== 'Container') return;
    if (touchDiff > threshold) {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % photos.length);
    } else if (touchDiff < -threshold) {
      setCurrentIndex((prevIndex) => (prevIndex - 1 + photos.length) % photos.length);
    }
  };

  const watermark = "https://thumbs2.imgbox.com/bd/de/rnNMvOFw_t.png";

  return (
    <div className='slider-container__secondary'
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
      ref={element}
    >
      {photos.map((p, index) => (
        <div
          key={p.id}
          className={currentIndex === index ? 'fade' : 'slide fade'}
        >
          {props.showWatermark && <img src={watermark} className='watermark'/>}
          <LazyLoadImage src={p.url} className={props.isBorderRounded ? 'photo-rounded' : 'photo'} alt={`Photo ${index}`} PlaceholderSrc="defaults/logoclear.png"/>
        </div>
      ))}
      {showDots && ( 
        <div className='dots'>
          <div className={`dots ${showDots ? 'fade-in' : 'fade-out'}`}>
            {photos.map((p, index) => (
              <span
                key={p.id}
                className={currentIndex === index ? 'dot active' : 'dot'}
                onClick={() => setCurrentIndex(index)}
              ></span>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}

export default Slider;